* {
    padding: 0;
    margin:  0;
}

.App {
    overflow-x: hidden;
    display: grid;
    justify-content: center;
    justify-items: center;
    align-content: start;
    --sunset: #fca644;
    --silhouette: #202036;
    font-family: "Readex Pro", sans-serif;
    background: rgb(253,62,45);
    background: linear-gradient(0deg, rgba(253,62,45,1) -15%, rgba(233,143,59,1) 33%, rgba(183,218,216,1) 65%, rgba(34,193,195,1) 110%);
    width: 100%;
    min-height: 100vh;
}

h1 {
    font-family: 'Pacifico', cursive, serif;
    color: var(--silhouette);
}

h1#title {
    font-size: 2.7rem;
    padding-top: 2rem;
}

#links {
    margin-top: 2rem;
    display: grid;
    grid-gap: 1rem;
    justify-items: center;
}

.Flourish {
    height: 4rem;
    margin: 1rem;
    fill: var(--silhouette);
}

.Flourish:nth-of-type(2n) {
    transform: scaleY(-1.0);
}

#links a {
    font-size: 2rem;
    color: var(--silhouette);
    text-decoration: none;
}

.center-text {
    display: grid;
    justify-items: center;
    margin-top: 1rem;
    font-size: 1.32rem;
}

a {
    text-decoration: none;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
